*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

body {
  color: var(--primary-text-color);
  background-color: var(--white-bg-color);
  font-family: DM Sans, sans-serif;
  font-weight: 500;
}

img {
  max-width: 100%;
  height: auto;
  margin: 0;
  display: block;
}

p, h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

.list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.link {
  color: currentColor;
  cursor: pointer;
  text-decoration: none;
}

button {
  cursor: pointer;
}

.section {
  padding-top: 60px;
}

html {
  scroll-behavior: smooth;
}

:root {
  --primary-text-color: #000;
  --secondary-text-color: #907e82;
  --light-text-color: #fff;
  --accent-color: #d41443;
  --white-bg-color: #fff;
  --light-bg-color: #fff5f6;
  --ice-cream-bg-color: #ffb8ca;
  --ice-cream-medium-color: #ffa5ba;
  --ice-cream-rich-color: #e17992;
  --ice-coffee-bg-color: #f0d1a5;
  --milkshakes-bg-color: #c2e297;
}

.container {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
}

@media screen and (min-width: 320px) {
  .container {
    max-width: 480px;
    padding-left: 20px;
    padding-right: 20px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    max-width: 1200px;
    padding-left: 16px;
    padding-right: 16px;
  }
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.header {
  width: 100%;
  z-index: 10;
  background-color: rgba(0, 0, 0, 0);
  padding-top: 8px;
  position: fixed;
  top: 0;
}

@media screen and (min-width: 768px) {
  .header {
    padding-top: 32px;
  }
}

@media screen and (min-width: 1200px) {
  .header {
    padding-top: 37px;
  }
}

.sticky {
  background-color: rgba(255, 184, 202, .9);
}

.header__container {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.logo {
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  display: block;
}

@media screen and (min-width: 1200px) {
  .logo {
