}

.mob-menu {
  height: 100vh;
  width: 100%;
  color: var(--light-text-color);
  background-color: var(--ice-cream-rich-color);
  z-index: 10;
  padding: 60px 0;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  right: 0;
  transform: translateX(100%);
}

@media screen and (min-width: 768px) {
  .mob-menu {
    width: 250px;
  }
}

@media screen and (min-width: 1200px) {
