    display: none;
  }
}

.mob-menu.is-open {
  transform: translateY(0);
}

.mob-menu__main-btn {
  width: 186px;
  margin: 0 auto 20px;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1), transform .25s cubic-bezier(.4, 0, .2, 1), box-shadow .25s cubic-bezier(.4, 0, .2, 1);
}

.mob-menu__main-btn:hover, .mob-menu__main-btn:focus {
  background-color: #ecebeb;
  transform: scale(1.1);
  box-shadow: 0 8px 50px rgba(210, 20, 67, .4);
}

.arrow-btn {
